	module.exports = {"data":{"markdownRemark":{"html":"<p><strong>Performance</strong> can be <em>Built, Fueled or Tuned</em>.</p>\n<p><strong>Built</strong> (Implementation and Techniques)</p>\n<ul>\n<li>Binary Search O(log n) is more efficient than Linear Search O(n)</li>\n<li>Caching can improve Disk I/O significantly boosting performance.</li>\n</ul>\n<p><strong>Fuelled</strong> (More Resources)</p>\n<ul>\n<li>Simply get a machine with more CPU(s) and Memory if constrained.</li>\n<li>Implement RAID to improve Disk I/O</li>\n</ul>\n<p><strong>Tuned</strong> (Settings and Configurations)</p>\n<ul>\n<li>Tune Garbage Collection to optimize Java Processes</li>\n<li>Tune Oracle parameters to get optimum database performance</li>\n</ul>\n<h3>Capacity and Load</h3>\n<p>Load is an Expectation out of system</p>\n<ul>\n<li>It is the rate of work that we put on the system.</li>\n<li>It is an factor external to the system.</li>\n<li>Load may vary with time and events.</li>\n<li>It has no upper cap, can increase infinitely</li>\n</ul>\n<p>Capacity is a Potential of the system</p>\n<ul>\n<li>It is the max rate of work, the system supports efficiently, effectively &#x26; infinitely</li>\n<li>It is a factor, internal to the system. Maximum capacity of a system is finite and stays fairly constant. We often call Throughput as the System’s Capacity for Load.</li>\n</ul>\n<p>Chemistry between Load &#x26; Capacity</p>\n<ul>\n<li>LOAD = CAPACITY? Good Expectation matches the potential. (Unreal)</li>\n<li>LOAD > CAPACITY? Bad Expectations is more than potential. (Reality)</li>\n<li>LOAD &#x3C; CAPACITY? Ugly Expectations is less than potential. (Waste)</li>\n</ul>\n<h3>Performance and Capacity Measurement</h3>\n<p>Response Time or LatencyMeasures of System’s Capacity</p>\n<ul>\n<li>Measures time spent executing a request (Round-trip time (RTT) for a Transaction)</li>\n<li>Good for understanding user experience</li>\n<li>Least scalable, Developers focus on how much time each transaction takes</li>\n</ul>\n<p>Throughput</p>\n<ul>\n<li>Measures the number of transactions executed over a period of time (Output Transactions per second (TPS))</li>\n<li>A measure of the system’s capacity for load</li>\n<li>Depending upon the resource type, It could be hit rate (for cache)</li>\n</ul>\n<p>Resource Utilization</p>\n<ul>\n<li>Measures the use of a resource (Memory, disk space, CPU, network bandwidth)</li>\n<li>Helpful for system sizing, is generally the easiest measurement to Understand</li>\n<li>Throughput and Response Time can conflict, because resources are limited</li>\n<li>Locking, resource contention, container activity</li>\n</ul>\n<h3><code>Handle mismatch between capacity and load (Throttling &#x26; Buffering Techniques)</code></h3>\n<p>No one stops us to load a system more than its capacity (Max Throughput).</p>\n<p>Transactions Per Seconds -Misconception, Real traffic may be in bursts</p>\n<ul>\n<li>Received 3600 transactions in a hour, not sure if every second only 60 were pumped</li>\n<li>Probably we received in bursts — all in first 10 minutes and for nothing last 50 minutes</li>\n<li>So we really cant say, at what tps? We can regulate bursts with throttling and buffering</li>\n</ul>\n<p>Throttling — (Implemented by producer to smoothen output)</p>\n<ul>\n<li>Spreads bursts over time to smoothen output from a process</li>\n<li>We may add throttles to control output rate from threads to each external interface Throttle of 10 tps ensures max output is 10 tps regardless of the load &#x26; capacity. Throttling is <strong>scheme for producers (</strong> Check production to rate the consumer can accept)</li>\n</ul>\n<p>Buffering — (Implemented by consumer to smoothen input)</p>\n<ul>\n<li>Spreads burst over time to smoothen input from an external interface</li>\n<li>We add buffering to control input rate to threads from each external interface Application processes input at 10 tps, load above it will be buffered &#x26; processed later Buffering is a <strong>scheme for consumers</strong> (Take whatever is produced, consume at our own)^</li>\n</ul>\n<h3>Supply Chain Principle <code>(Apply it to define a optimum Thread Pool Size)</code></h3>\n<p>Thread is an abstract CPU unit resource here.</p>\n<p>The more throughput you want, more will be the resource consumption.</p>\n<p>You may apply this principle to define the optimum thread-pool size for a system/application.</p>\n<p>— To support a Throughput (t) transactions per second- (t) = <em>20 tps</em></p>\n<p>— Where each transaction takes (d) seconds to complete- (d) = <em>5 seconds</em></p>\n<p>— We need (d<em>t) threads at least (min size of the thread pool)- (d</em>t) = <em>100 threads</em></p>\n<pre><code>To support a Throughput (t) of 20 tps Where each transaction takes(d) 5 seconds We need 100 (d*t) threads at least\n</code></pre>\n<p><img src=\"https://cdn-images-1.medium.com/max/800/1*4WulnV-VoRnR-t8_e3j1gA.png\"></p>\n<p><em>100 threads kept busy executing 5 batches of 20 (100 transactions) where each takes 5 seconds to complete ( A batch of 20 coming in and leaving every second)</em></p>\n<h3>Quantify Resource Consumption</h3>\n<h3>Utilization &#x26; Saturation</h3>\n<p>Resource Utilization</p>\n<ul>\n<li>Utilization measures how busy a resource is.</li>\n</ul>\n<p>Resource Saturation</p>\n<ul>\n<li>Saturation is often a measure of work that has queued waiting for the resource</li>\n<li>It can be measured as both</li>\n<li>As an average over time</li>\n</ul>\n<p>For some resources that do not queue, saturation may be synthesized by error counts. Example Page-Faults reveal memory saturation.</p>\n<p>Load (input rate of requests) is an independent/external variable, user of a system can at any point be overloading or under consuming the system.</p>\n<p>Resource consumption, Throughput (out-put rate of response) are a function of load and dependent on internal variables (threads for cpu, queues for memory)</p>\n<h3>How Load, Resource Consumption and</h3>\n<h3>Throughput related?</h3>\n<ul>\n<li>As load increases, throughput increases, until maximum resource utilization on the bottleneck device is reached. At this point, maximum possible throughput is reached, Saturation occurs.</li>\n<li>Then, queuing (waiting for saturated resources) starts to occur.</li>\n<li>Queuing typically manifests itself by degradation in response times.</li>\n<li>This phenomenon is described by Little’s Law:</li>\n</ul>\n<p>L = X * R</p>\n<p>L (LOAD), X (THROUGHPUT) and R (RESPONSE TIME)</p>\n<ul>\n<li>As L increases, X increases (R also increases slightly, because there is always some level of contention at the component level).</li>\n<li>At some point, X reaches Xmax — the maximum throughput of the system. At this point, as L continues to increase, the response time R increases in proportion and through-put may then start to decrease, both due to resource contention.</li>\n</ul>\n<p><img src=\"https://cdn-images-1.medium.com/max/800/1*clX08uls7KNzOGNpaT906g.png\"></p>\n<p><em>Performance pattern of a Concurrent Process</em></p>\n<h3>How Throughput and Resource Consumption are related? (Example)</h3>\n<p>Throughput &#x26; Latency can have an inverse or direct relationship. Concurrent tasks (Threads) often contend for resources (locking &#x26; contention)</p>\n<blockquote>\n<p>Single-Threaded — Higher Throughput = Lower Latency</p>\n</blockquote>\n<blockquote>\n<ul>\n<li>Consistent throughput, does not increase with incoming load &#x26; resources</li>\n</ul>\n</blockquote>\n<blockquote>\n<ul>\n<li>Processes serially, Good for batch jobs</li>\n</ul>\n</blockquote>\n<blockquote>\n<ul>\n<li>Response Time linearly varies with request order.</li>\n</ul>\n</blockquote>\n<blockquote>\n<p>Multi-Threaded — Higher Throughput = Higher Latency (Most of the time)</p>\n</blockquote>\n<blockquote>\n<ul>\n<li>Throughput may increase linearly with load, it starts to drop after threshold</li>\n</ul>\n</blockquote>\n<blockquote>\n<ul>\n<li>Process Concurrently, Good for interactive modules (Web Apps)</li>\n</ul>\n</blockquote>\n<blockquote>\n<ul>\n<li>Near consistent Response Time, doesn’t vary much with order but load. Single Threaded — 10 CPU(s) Multi Threaded — 10 CPU(s) Threads = 1 Latency = .1 seconds Throughput = 1/.1 = 10 tx/sec</li>\n</ul>\n</blockquote>\n<p><img src=\"https://cdn-images-1.medium.com/max/800/1*cRtzxQtYReWKTghtd-TD5w.png\"></p>\n<p><em>Non linear vertical scaling with threads due to cpu latency from context switching</em></p>\n<h3>Producer Consumer Principle</h3>\n<ul>\n<li>The Utilization Law: Ui = T * Di</li>\n<li>Where Ui is the percentage of utilization of a device in the application, T is the application throughput, and Di is the service demand of the application device.</li>\n<li>The maximum throughput of an application Tmax is limited by the maximum service demand of all of the devices in the application.</li>\n<li>EXAMPLE — A load test reports 200 kb/sec average throughput:</li>\n</ul>\n<blockquote>\n<p>CPUavg = 80% | Dcpu = 0.8 / 200 kb/sec = 0.004 sec/kb</p>\n</blockquote>\n<blockquote>\n<p>Memoryavg = 30% | Dmemory = 0.3 / 200 kb/sec = 0.0015 sec/kb</p>\n</blockquote>\n<blockquote>\n<p>Diskavg = 8% | Ddisk = 0.08 / 200 kb/sec = 0.0004 sec/kb</p>\n</blockquote>\n<blockquote>\n<p>Network I/Oavg = 40% | Dnetwork I/O = 0.4 / 200 kb/sec = 0.002 sec/kb</p>\n</blockquote>\n<ul>\n<li>In this case, Dmax corresponds to the CPU. So, the CPU is the bottleneck device.</li>\n<li>We can use this to predict the maximum throughput of the application by setting the CPU utilization to 100% and dividing by Dcpu. In other words, for this example: Tmax = 1 / Dcpu = 250 kb/sec</li>\n<li>In order to increase the capacity of this application, it would first be necessary to increase CPU capacity. Increasing memory, network capacity or disk capacity would have little or no effect on performance until after CPU capacity has been increased sufficiently.</li>\n</ul>\n<h3><strong>Work Pools (Queues) &#x26; Thread Pools Working Together</strong></h3>\n<p>Work Pools are queues of work to be performed by a software application or component.</p>\n<ul>\n<li>If all threads in thread pool are busy, incoming work can be queued in work pool</li>\n<li>Threads from thread pool, when freed can execute them later</li>\n</ul>\n<p>Work Pools cover up congestion &#x26; smoothen bursts</p>\n<ul>\n<li>A queue consisting of units of work to be performed</li>\n<li>CONGESTION, by allowing the current (client) threads to submit work and return</li>\n<li>BURST, over capacity transaction can buffered in work pool and executed later</li>\n<li>Allow for caching of units of work to reduce system intensive calls —( Ex. we can perform a bulk fetch form a database instead of fetching on record at a time)</li>\n</ul>\n<h3>Queuing Tasks may be risky</h3>\n<ul>\n<li>One task could lock up another that would be able to continue if the queued task were to run.</li>\n<li>Queuing can smoothen in-coming traffic burst limited in time (depending upon the rate of traffic and size)</li>\n<li>Fails if traffic arrives on average faster than they can be processed.</li>\n<li>In general, Work Pools are in memory so it is important to understand what the impact of restarting a system is, as in memory elements will be lost.</li>\n</ul>\n<h3>Bounded &#x26; Unbounded Pools (Load Shedding)</h3>\n<p>If not bounded, pools can grow freely but can cause system to exhaust resources.</p>\n<ul>\n<li>Work Pool / Queue Unbounded — (May overload Memory / Heap &#x26; crash) — Each work object in the queue stays holding the space until consumed</li>\n<li>Thread Pool Unbounded — (May overload CPU / Native Space and Crash) — Each thread asks to be scheduled on CPU and consumes native stack space</li>\n</ul>\n<p>If queue size is bounded, incoming execute requests block when it is full. We can apply different Policies to handle t, for example</p>\n<p>— Reject if there is no space (Can have side affects)</p>\n<p>— Remove based on Priority — (Ex priority may be function of time — Timeouts)</p>\n<p>Thread Pools can have different policies when Work Pools is full:</p>\n<ul>\n<li>Block till there is available space — Starve (VERY BAD — Sometimes Needed)</li>\n<li>Run in Current Thread (Very Dangerous!)</li>\n</ul>\n<h3>Work pool &#x26; thread pool sizes can often be traded off for each other</h3>\n<p><strong>Large Work-Pool and small thread pools</strong></p>\n<ul>\n<li>Minimizes CPU usage, OS resources, and context-switching overhead.</li>\n<li>Can lead to artificially low throughput especially if tasks frequently block (ex I/O bound)</li>\n</ul>\n<p><strong>Small Work pool generally require larger thread pool sizes</strong></p>\n<ul>\n<li>Keeps CPUs busier</li>\n<li>May cause scheduling overhead (Context Switching) and may lessen throughput. Especially if the number of CPUs are less.</li>\n</ul>\n<hr>\n<p>This article is also aviable on my <a href=\"https://medium.com/simars/performance-concurrency-threads-throttling-memory-queues-f5672190b606?source=friends_link&#x26;sk=d17a478b5e2ca71e94ee467de99777a3\"><strong>Medium</strong></a> publication. If you like the artile, or have any comments and suggestions, please <strong><em>clap</em></strong> or leave <strong><em>comments</em></strong> on <a href=\"https://medium.com/simars/performance-concurrency-threads-throttling-memory-queues-f5672190b606?source=friends_link&#x26;sk=d17a478b5e2ca71e94ee467de99777a3\"><strong>Medium</strong></a>.</p>","frontmatter":{"path":"/performance-concurrency","title":"Performance & Concurrency | Build Fuel Tune","author":"Simar Paul Singh","date":"2019-02-26"}}},"pathContext":{}}

